* {                   
  box-sizing: border-box;
}

body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}
.Column {
    background-color: silver;
}
.Cell {
    display: block;
    width: 70px;
    height: 70px;
    border-radius: 100%;
    padding: 0;
    border: 5px solid silver;
    -moz-user-select: none;
    -ms-user-select: none; 
    -o-user-select: none; 
    -webkit-user-select: none;
    transition: all 0.1s ease;
  }
  
  .Cell:active, .Cell:focus {
    outline: none;
  }
  
  .Cell::-moz-focus-inner {
    border: 0;
  }
  
  @media all and (max-width: 600px) {
    .Cell {
      width: 40px;
      height: 40px;
    }
  
  }
  
  .white {
    background-color: white;
  }
  
  .red {
    background-color: tomato;
  }
  
  .blue {
    background-color: steelblue;
  }
.Grid {
    display: -webkit-flex;
    display: flex;
    -webkit-align-items: center;
            align-items: center;
    margin-top: 20px;
    border: 7px solid silver;
    border-radius: 10px;
    background-color: silver;
}
.Caption {
    margin: 5px;
}

.Caption-Text {
  color: rgb(44, 44, 44);
  font-size: 18pt;
}

@media all and (max-width: 600px) {
  .Caption-Text {
    font-size: 14pt;
  }
}
.RestartButton {
    font-size: 10pt;
    border: 1px solid gainsboro;
    border-radius: 5px;
    background-color:gainsboro;
    box-shadow: 1px 1px 2px darkgrey;
    padding: 14px 28px;
    display: block;
    -moz-user-select: none;
    -ms-user-select: none; 
    -o-user-select: none; 
    -webkit-user-select: none;
  }
  
  .RestartButton:active, .RestartButton:focus {
    outline: none;
  }
  
  .RestartButton::-moz-focus-inner {
    border: 0;
  }
  
  .RestartButton:hover {
    border: 1px solid silver;
  }
  
  .RestartButton:hover:active {
    background-color: rgb(210, 210, 210);
  }
.App {
    display: -webkit-flex;
    display: flex;
    -webkit-align-items: center;
            align-items: center;
    -webkit-flex-direction: column;
            flex-direction: column;
}
